.header {
  padding: 14px 0;
  background-color: #fff;
  border-bottom: 1px solid $colorLight; }

.logo {
  margin-right: auto;
  max-width: 58px;

  a {
    display: block;
    outline: none; } }

.main-menu,
.header-info {
  display: none; }

.header-btns {
  display: flex;
  align-items: center; }

.btn_head-order {
  padding: 0;
  padding-left: 2px;
  width: 38px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;

  span {
    display: none; }

  .ico {
    vertical-align: top;
    fill: #fff;
    font-size: 24px; } }

.cart-round {
  margin-left: 8px;
  width: 38px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border: 1px solid $colorGrayLight;
  border-radius: 50%;
  transition: $transDefault;

  &:hover {
    background-color: $colorLight; }

  .ico {
    font-size: 20px; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .header {
    padding: 0; }

  .header-info {
    display: block;
    padding: 9px 0;
    border-bottom: 1px solid $colorLight; }

  .h-work-time {
    display: none; }

  .h-calls-info {
    display: inline-block;
    vertical-align: top;
    font-size: 14px;
    line-height: 22px;

    span {
      margin-right: 15px;

      a {
        color: $colorDark;

        &:hover {
          color: $colorPrimaryDark; } } } }

  .header-info-nav {
    li {
      display: inline-block;
      vertical-align: top;
      font-size: 14px;
      line-height: 22px;
      margin-left: 20px; } }

  .header-main {
    padding: 19px 0; }

  .logo {
    max-width: 71px;
    margin-right: 10px; }

  .cart-round {
    position: relative;
    width: 48px;
    height: 48px;
    margin-left: 10px;

    .ico {
      font-size: 23px; } }

  .cart-round-notif {
    position: absolute;
    right: -7px;
    top: 0;
    width: 20px;
    height: 20px;
    background-color: $colorDanger;
    border-radius: 50%; }

  .btn_head-order {
    width: auto;
    height: auto;
    padding: 11px 20px 13px;
    border-radius: 24px;

    span {
      display: block; }

    .ico {
      display: none; } }

  .main-menu {
    display: block;
    margin-right: auto; }

  .menu {
    li {
      display: inline-block;
      vertical-align: top;
      font-size: 16px;
      line-height: 21px;
      margin-left: 32px;
      color: $colorDark;

      &.menu-hidden-tablet {
        display: none; }

      a {
        color: $colorDark;

        &:hover {
          color: $colorGray; } } }

    .menu__dd {
      position: relative;

      &.opened {
        .menu-dd-btn {
          .ico {
            transform: rotate(-180deg); } } }

      ul {
        display: none;
        position: absolute;
        left: 0;
        top: 100%;
        margin-top: 24px;
        z-index: 10;
        padding: 5px 0;
        background: #FFFFFF;
        box-shadow: 0px 9px 25px rgba(0, 0, 0, 0.15);
        border-radius: 8px;
        overflow: hidden;

        li {
          display: block;
          margin: 0;

          a {
            display: block;
            padding: 0 20px;
            line-height: 48px;
            white-space: nowrap;
            transition: $transDefault;

            &:hover {
              background-color: $colorPrimaryDark;
              color: #fff; } } } } }

    .menu-dd-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      transition: $transDefault;

      &:hover {
        color: $colorGray; }

      .ico {
        margin-left: 8px;
        margin-top: 3px;
        font-size: 6px;
        fill: none;
        stroke: $colorPrimary;
        transition: $transDefault; } } }

  .header-info {
    line-height: 22px; } }
/* end media min width 740 pixels */

/* Min width 1100 pixels */
@media only screen and (min-width: 1100px) {
  .main-menu {
    margin-left: auto;
    margin-right: auto; }

  .menu {
    li {
      &:first-child {
        margin-left: 0; }

      &.menu-hidden-tablet {
        display: inline-block; } } }

  .h-work-time {
    display: inline-block;
    vertical-align: top;
    margin-right: 65px;
    font-size: 14px;
    line-height: 22px; } }
/* end media min width 1100 pixels */

/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .header-info-nav {
    li {
      margin-left: 36px; } }

  .cart-round {
    margin-left: 20px; } }
/* end media min width 1200 pixels */
