.filter_open {
  &:before {
    width: 100%;
    height: 100%;
    background-color: rgba(4, 47, 89, .7); }

  .filter-wrapp {
    transform: translateX(0); } }

.filter-wrapp {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 50;
  transform: translateX(-100%);
  transition: .4s ease-in-out; }

.filter {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  height: 100%; }


.filter-mobile-head {
  display: flex;
  align-items: center;
  padding: 25px 14px;

  .ico-mono-close {
    vertical-align: top;
    margin-right: 30px;
    font-size: 20px;
    fill: $colorPrimary; }

  .title-site {
    margin-bottom: 0; } }

.filter-close {
  margin-top: 3px; }

.filter-options-wrapp {
  padding: 10px 12px;
  height: 100vh;
  overflow: auto;

  .btn[type="reset"] {
    margin-top: 11px; } }

.filter-items {
  margin: 0 -12px 35px; }

.filter-item {
  &__tt {
    position: relative;
    display: block;
    padding: 10px 50px 10px 12px;
    font-size: 16px;
    line-height: 21px;
    cursor: pointer;

    &.open {
      &::before {
        transform: none; } }

    &::before {
      content: '';
      position: absolute;
      right: 18px;
      top: 11px;
      width: 19px;
      height: 19px;
      background-image: url('../img/arrow-angle-mob.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      transform: rotate(180deg);
      transition: $transDefault; } } }

.filter-item-options {
  display: none;
  margin: 13px 0 25px;
  padding: 20px 12px 17px;
  background-color: $colorLight;

  li {
    margin-bottom: 13px; }

  .fake-control {
    background-color: transparent; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .filter_open {
    &:before {
      display: none; } }

  .filter-wrapp {
    position: relative;
    height: auto;
    transform: none; }

  .filter {
    background: none;
    height: auto; }

  .filter-mobile-head,
  .filter-mob-btn {
    display: none; }

  .filter-options-wrapp {
    padding: 0;
    height: auto; }

  .filter-items {
    margin: 0 0 32px; }

  .filter-item {
    background-color: $colorLight;
    border: 1px solid #EFEFEF;
    border-radius: 20px;
    margin-bottom: 10px;

    &__tt {
      position: relative;
      display: block;
      padding: 20px 30px 20px 15px;
      font-size: 14px;
      line-height: 18px;
      font-weight: 700;
      text-transform: uppercase;

      &::before {
        right: 11px;
        top: 24px;
        width: 10px;
        height: 10px;
        background-image: url('../img/arrow-top.svg'); } } }

  .filter-item-options {
    display: none;
    margin: 0;
    padding: 0 15px 15px;
    background: none;

    li {
      margin-top: 1px; } } }
/* end media min width 740 pixels */

/* Min width 1100 pixels */
@media only screen and (min-width: 1100px) {
  .filter-item {
    &__tt {
      padding-right: 50px;

      &::before {
        right: 22px; } } }

  .filter-options-wrapp {
    .btn {
      width: auto; }

    .btn[type="reset"] {
      margin: 0 0 0 5px; } } }
/* end media min width 1100 pixels */
