.faq-list {
  li {
    margin: 0 0 20px;

    &.opened {
      .faq-header {
        &__icon {
          transform: rotate(0); } } } } }

.faq-header {
  position: relative;
  padding-right: 60px;
  font-weight: 700;
  font-size: 21px;
  line-height: 28px;
  color: $colorPrimary;
  transition: $transDefault;
  cursor: pointer;

  &:hover {
    color: $colorPrimaryDark; }

  &__icon {
    position: absolute;
    top: 0;
    right: 0;
    width: 43px;
    height: 43px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $colorPrimary;
    border-radius: 50%;
    transform: rotate(180deg);
    transition: $transDefault;

    .ico {
      margin-bottom: 2px;
      font-size: 9px; } } }

.faq-body {
  margin-top: 10px;
  display: none;

  p {
    margin-bottom: 0; }

  p + p {
    margin-top: 20px; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .faq-list {
    li {
      margin-bottom: 30px; } }

  .faq-header {
    font-size: 28px;
    line-height: 36px; }

  .faq-body {
    margin-top: 28px;

    p + p {
      margin-top: 25px; } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .faq-header {
    font-size: 32px;
    line-height: 42px; }

  .faq-body {
    margin-top: 20px; } }
/* end media min width 980 pixels */
