.table-overflow-mobile {
  margin: 0 -12px 20px;
  padding-left: 12px;
  padding-right: 12px;
  overflow-x: auto;

  table {
    min-width: 500px;
    margin-bottom: 10px; } }

table {
  width: 100%;
  margin-bottom: 30px;
  border-collapse: collapse;
  font-size: 16px;
  line-height: 21px;

  tr {
    &:last-child {
      td {
        border-bottom: none; } } }

  td, th {
    padding: 10px 12px;
    border: 1px solid $colorGrayLight;

    &:first-child {
      border-left: none; }

    &:last-child {
      border-right: none; } }

  th {
    font-weight: normal;
    text-align: left;
    background-color: $colorLight;
    border-bottom: 1px solid $colorGray; } }

/* Max width 739 pixels */
@media only screen and (max-width: 739px) {
  .table-flex-mobile {
    display: block;

    tbody, tr, td, th, tfoot, thead {
      display: block;
      border: 0;
      padding: 0; }

    tr {
      padding: 5px 15px 7px;
      border-bottom: 1px solid $colorGrayLight;

      &:first-child {
        border-top: 1px solid $colorGrayLight; } } } }
/* end media max width 739 pixels */

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .table-overflow-mobile {
    margin-left: -25px;
    margin-right: -25px;
    padding-left: 25px;
    padding-right: 25px; }

  .table-flex-mobile,
  table {
    td, td {
      padding: 19px 20px; } }

  .table-first-child-fix-width {
    width: 300px; } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .table-flex-mobile,
  table {
    td, td {
      padding: 16px 20px;
      font-size: 18px;
      line-height: 27px; } } }
/* end media min width 980 pixels */
