  cursor: pointer;
  text-indent: -9999px;
  color: #3099FF;
  background-image: url("../img/close.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 17px;
  transition: 0.2s ease;
}

button.mfp-close::before,
.mfp-close::before,
button.mfp-arrow::before {
  display: none;
}

button.mfp-close,
.mfp-close {
  top: 13px;
  right: 10px;
  width: 35px;
  height: 35px;
  opacity: 1;
}

button.mfp-close:active,
.mfp-close:active {
  top: 13px;
}

button.mfp-arrow {
  background-color: #000;
  padding-right: 5px;
}

button.mfp-arrow::after {
  display: none;
}

button.mfp-arrow.mfp-arrow-right {
  padding: 0 0 0 5px;
}

button.mfp-arrow.mfp-arrow-right .icon {
  transform: rotate(-180deg);
}

button.mfp-arrow .icon {
  width: 30px;
  height: 30px;
  fill: #fff;
}

.mfp-zoom-out-cur {
  cursor: default;
}

.mfp-zoom-out-cur .mfp-image-holder .mfp-close {
  cursor: pointer;
}

.magnific-gallery button {
  margin-right: 5px;
  height: 100px;
}

.magnific-gallery button img {
  max-height: 100%;
}

.mfp-figure {
  position: relative;
}

.mfp-figure::after {
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.3);
}

.mfp-figure img.mfp-img {
  padding: 0;
}

.mfp-bottom-bar {
  display: flex;
  justify-content: space-between;
  margin-top: 0;
  padding: 5px;
}

.mfp-title {
  margin-right: 10px;
  font-size: 14px;
  line-height: 24px;
}

.mfp-counter {
  position: relative;
  font-size: 14px;
  line-height: 24px;
}

