.title-site, h1, h2, h3, h4, h5, h6 {
  display: block;
  margin: 0 0 20px;
  padding: 0;
  font-family: $site-font-family, $systemFont, sans-serif;
  font-weight: normal;
  line-height: 21px;
  color: $colorDark; }

h1, .title-site_h1 {
  font-size: $site-h1-size-mobile;
  line-height: 28px;
  font-weight: 700; }

h2, .title-site_h2 {
  font-size: $site-h2-size-mobile;
  line-height: 28px;
  font-weight: 700; }

h3, .title-site_h3 {
  font-size: $site-h3-size-mobile;
  line-height: 24px; }

h4, .title-site_h4 {
  font-size: $site-h4-size-mobile;
  margin-bottom: 10px; }

h5, .title-site_h5 {
  font-size: $site-h5-size-mobile;
  margin-bottom: 10px; }

h6, .title-site_h6 {
  font-size: $site-h6-size-mobile;
  margin-bottom: 10px; }

.title-site_icon {
  padding-left: 37px;
  position: relative;

  .ico {
    position: absolute;
    left: 0;
    top: 5px;
    fill: $colorPrimary; }

  .ico-mono-sim {
    top: 6px;
    font-size: 18px; }

  .ico-mono-device-headph {
    left: 7px;
    font-size: 17px; }

  .ico-mono-ruble-round {
    left: 2px;
    font-size: 21px; } }

ul, ol {
  list-style-type: none;
  margin: 0;
  padding: 0; }

li {
  margin: 0;
  padding: 0; }

p {
  margin: 0 0 20px; }

.text-bl {
  p {
    &:last-child {
      margin-bottom: 0; } }

  img {
    display: block;
    margin-bottom: 20px; }

  &__inner {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0; } }

  h2, .title-site_h2,
  h3, .title-site_h3 {
    margin-bottom: 10px; }

  ul {
    margin-bottom: 20px;

    li {
      margin: 4px 0;

      &::before {
        content: '';
        margin-right: 5px;
        color: $colorPrimary; } } }

  ol {
    margin: 0 0 20px;
    counter-reset: ol-list;

    li {
      &::before {
        margin-right: 5px;
        counter-increment: ol-list;
        content: counters(ol-list,".");
        color: $colorGray; } } }

  li {
    margin-bottom: 10px; }

  .ol-list-num {
    li {
      position: relative;
      padding-left: 37px;
      margin-bottom: 20px;

      &::before {
        position: absolute;
        top: -3px;
        left: 0;
        width: 25px;
        height: 25px;
        line-height: 24px;
        text-align: center;
        color: $colorDark;
        background-color: $colorLight;
        border: 1px solid $colorGrayLight;
        border-radius: 50%; } }

    &_spec-tiles {
      li {
        margin: 5px 0;
        font-size: 18px;
        line-height: 24px;
        padding: 15px 20px 17px 60px;
        border: 1px solid #EFEFEF;
        border-radius: 5px;

        &::before {
          width: 33px;
          height: 33px;
          font-size: 18px;
          top: 11px;
          left: 10px;
          border: none; } } } } }

.text_center {
  text-align: center; }

.text_left {
  text-align: left; }

.text_right {
  text-align: right; }

.text_uppercase {
  text-transform: uppercase; }

.color-light {
  color: $colorGray; }

.max-w-img {
  width: 100%;

  img {
    width: 100%; } }

img {
  vertical-align: middle;
  max-width: 100%; }

a {
  text-decoration: none;
  color: $colorPrimary;
  transition: $transDefault;

  &:hover {
    text-decoration: none;
    color: $colorPrimaryDark; } }

b, strong {
  font-weight: 700; }

.icon {
  display: inline-block;
  vertical-align: middle; }

.text-col-aside {
  font-size: 14px;
  line-height: 18px; }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  h1, .title-site_h1 {
    font-size: $site-h1-size-tablet;
    line-height: 36px; }

  h2, .title-site_h2 {
    font-size: $site-h2-size-tablet;
    line-height: 36px; }

  h3, .title-site_h3 {
    font-size: $site-h3-size-tablet;
    line-height: 28px; }

  h4, .title-site_h4 {
    font-size: $site-h4-size-tablet; }

  h5, .title-site_h5 {
    font-size: $site-h5-size-tablet; }

  h6, .title-site_h6 {
    font-size: $site-h6-size-tablet; }

  .text-bl {
    &__inner {
      margin-bottom: 30px; }

    .ol-list-num {
      margin-top: 20px;

      li {
        padding-left: 60px;

        &::before {
          top: -5px;
          width: 28px;
          height: 28px;
          line-height: 27px; } }

      &_spec-tiles {
        li {
          margin: 10px 0;
          font-size: 20px;
          line-height: 28px;
          padding: 27px 20px 32px 81px;

          &::before {
            top: 20px;
            left: 21px;
            width: 42px;
            height: 42px;
            line-height: 41px; } } } } }

  .title-site_icon {
    padding-left: 60px;

    .ico {
      left: 0;
      top: 7px; }

    .ico-mono-sim {
      font-size: 22px; }

    .ico-mono-device-headph {
      left: 9px;
      font-size: 20px; }

    .ico-mono-ruble-round {
      top: 6px;
      left: 2px;
      font-size: 26px; } }

  .text-col-aside {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;

    p {
      width: 48.8%; } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  body {
    font-size: 18px;
    line-height: 24px; }

  h1, .title-site_h1 {
    font-size: $site-h1-size;
    line-height: 42px; }

  h2, .title-site_h2 {
    font-size: $site-h2-size;
    line-height: 42px; }

  h3, .title-site_h3 {
    font-size: $site-h3-size;
    line-height: 32px; }

  h4, .title-site_h4 {
    font-size: $site-h4-size;
    line-height: 24px; }

  h5, .title-site_h5 {
    font-size: $site-h5-size; }

  h6, .title-site_h6 {
    font-size: $site-h6-size-mobile; }

  .text-bl {
    .ol-list-num {
      li {
        padding-left: 50px; }

      &_spec-tiles {
        display: flex;
        justify-content: space-between;

        li {
          width: 32.3%;
          margin: 0;
          font-size: 21px;
          line-height: 32px;
          padding: 84px 50px 25px 30px;

          &::before {
            top: 30px;
            left: 31px;
            font-size: 21px; } } } } }

  .title-site_icon {
    padding-left: 50px;

    .ico {
      top: 10px; } } }
/* end media min width 980 pixels */

/* Min width 1100 pixels */
@media only screen and (min-width: 1100px) {
  .text-col-aside {
    margin-top: 0;
    margin-left: 20px;
    display: block;
    max-width: 280px;
    min-width: 280px;

    p {
      width: 100%; } } }
/* end media min width 1100 pixels */
