.swiper-slide {
  height: auto; }

.swiper-tarifs,
.swiper-tarifs-destroy-desktop {
  overflow: visible;

  .swiper-slide {
    flex-basis: 263px;

    .tarif-item {
      height: 100%; } } }

.swiper-nav-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 30px; }

.swiper-pagination {
  line-height: 16px; }

.swiper-pagination-bullet {
  display: inline-block;
  vertical-align: top;
  width: 16px;
  height: 16px;
  margin: 0 7px;
  background-color: #fff;
  border: 1px solid $colorPrimary;
  border-radius: 50%;
  transition: $transDefault;
  cursor: pointer;

  &-active {
    background-color: $colorPrimary;
    cursor: default; }

  &:hover {
    background-color: $colorPrimary; } }

.swiper-button-prev,
.swiper-button-next {
  display: none;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid $colorPrimary;
  transition: $transDefault;

  &:hover {
    background-color: $colorPrimary;

    .ico {
      fill: #fff; } }

  .ico {
    fill: $colorPrimary; } }

.swiper-button-prev {
  margin-right: 24px;
  padding-right: 1px; }

.swiper-button-next {
  margin-left: 24px;
  padding-left: 1px; }

.swiper-operator-wrapper {
  margin-bottom: 65px; }

.swiper-operator {
  overflow: visible;

  .operator-sim-item {
    width: 100%; } }

.swiper-review {
  overflow: visible;

  .swiper-slide {
    flex-basis: 263px; }

  .swiper-nav-wrapper {
    margin-top: 20px; } }

.swiper-review-item {
  height: 100%;
  padding: 30px 20px 40px;
  background-color: $colorLight;
  border-radius: 8px;

  p {
    margin-bottom: 0; }

  p + p {
    margin-top: 20px; }

  .user-round {
    margin-bottom: 30px; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .swiper-button-prev,
  .swiper-button-next {
    display: inline-flex; }

  .swiper-operator-wrapper {
    position: relative;
    max-width: 674px;
    padding: 0 70px;
    margin: 0 auto 40px;

    .swiper-pagination {
      display: none; }

    .swiper-nav-wrapper {
      position: absolute;
      top: 50%;
      left: 0;
      z-index: 3;
      width: 100%;
      margin: 0;
      height: 1px;
      justify-content: space-between; } }

  .swiper-operator {
    overflow: hidden; }

  .swiper-review-item {
    padding: 15px 25px 30px;

    .user-round {
      margin-bottom: 20px; } }

  .swiper-nav-wrapper {
    margin-top: 20px; } }
/* end media min width 740 pixels */

/* Max width 979 pixels */
@media only screen and (max-width: 979px) {
  .swiper-review-item {
    .user-round {
      &__avatar {
        margin-right: 20px;
        min-width: 30px;
        max-width: 30px;
        height: 30px; }

      .ur-av_user-pic {
        .ico {
          font-size: 28px;
          margin-left: -13px; } } } } }
/* end media max width 979 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .swiper-tarifs,
  .swiper-tarifs-destroy-desktop {
    .swiper-nav-wrapper {
      margin-top: 50px; }

    .swiper-slide {
      flex-basis: 280px; } }

  .swiper-operator-wrapper {
    max-width: 944px;
    padding: 0 80px;
    margin-bottom: 50px; }

  .swiper-review {
    .swiper-slide {
      flex-basis: 480px; }

    .swiper-nav-wrapper {
      margin-top: 30px; } }

  .swiper-review-item {
    padding: 35px 40px 50px;

    .user-round {
      margin-bottom: 50px; } }

  .swiper-nav-wrapper {
    margin-top: 30px; } }
/* end media min width 980 pixels */

/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .swiper-tarifs {
    overflow: hidden; }

  .swiper-operator-wrapper {
    max-width: 1140px; }

  .swiper-tarifs-destroy-desktop {
    .swiper-wrapper {
      flex-wrap: wrap;
      width: auto;
      margin-right: -20px;
      margin-top: -50px;
      transform: none !important; }

    .swiper-slide {
      margin-top: 50px;
      margin-right: 20px; }

    .swiper-nav-wrapper {
      display: none; } } }
/* end media min width 1200 pixels */
