.about-company {
  &__main {
    margin-bottom: 30px;

    .swiper-operator-wrapper {
      margin-bottom: 0; }

    h2, .title-site_h2 {
      margin-bottom: 10px; }

    p {
      margin-bottom: 15px; } }

  &__info {
    margin-bottom: 30px;

    h2, .title-site_h2 {
      margin-bottom: 10px; }

    p {
      margin-bottom: 20px; } }

  &__reviews {
    > h2, > .title-site_h2, > p {
      margin-bottom: 15px; } }

  &__services {
    padding-bottom: 10px;

    h2, .title-site_h2 {
      margin-bottom: 30px; } } }

.about-partners {
  p {
    margin-bottom: 18px;
    font-size: 18px;
    line-height: 24px; } }

.about-tiles {
  margin-bottom: 30px;

  li {
    padding: 15px 10px 20px;
    margin: 5px 0;
    font-size: 18px;
    line-height: 24px;
    color: #fff;
    background-color: $colorPrimary;
    border-radius: 5px; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .about-partners {
    p {
      font-size: 20px;
      line-height: 28px; } }

  .about-company {
    &__info {
      h2, .title-site_h2 {
        margin-bottom: 15px; }

      p {
        margin-bottom: 25px; } }

    &__reviews {
      > p {
        margin-bottom: 25px; } }

    &__services {
      h2, .title-site_h2 {
        margin-bottom: 35px; }

      .main-icons {
        li {
          font-size: 14px;
          line-height: 18px; } } } }

  .about-tiles {
    li {
      padding: 25px 10px 34px;
      font-size: 20px;
      line-height: 28px; } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .about-company {
    &__main {
      margin-bottom: 50px;

      .swiper-operator-wrapper {
        margin-left: 0;
        margin-right: 0; }

      h2, .title-site_h2 {
        margin-bottom: 20px; } }

    &__info {
      margin-bottom: 50px;

      h2, .title-site_h2 {
        margin-bottom: 32px; } }

    &__reviews {
      > p {
        max-width: 980px;
        margin-bottom: 35px; } }

    &__services {
      padding-bottom: 20px;

      h2, .title-site_h2 {
        margin-bottom: 25px; } } }

  .about-partners {
    p {
      font-size: 21px;
      line-height: 32px;
      margin-bottom: 18px; } }

  .about-tiles {
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;

    li {
      font-size: 21px;
      line-height: 32px;
      width: 32.3%;
      padding: 20px 30px 37px; } } }
/* end media min width 980 pixels */
