.guarantee-wrapper {
  .title-site_icon {
    margin-bottom: 15px; }

  .text-bl {
    &__inner {
      margin-bottom: 30px;

      &:last-child {
        margin-bottom: 0; } } } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .guarantee-wrapper {
    .title-site_icon {
      margin-bottom: 20px; }

    .text-bl {
      &__inner {
        margin-bottom: 40px; } } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .guarantee-wrapper {
    .title-site_icon {
      margin-bottom: 25px; }

    .text-bl {
      &__inner {
        margin-bottom: 50px; } } } }
/* end media min width 980 pixels */

/* Min width 1100 pixels */
@media only screen and (min-width: 1100px) {
  .text-row-wrapp {
    display: flex; }

  .text-col-main {
    flex-grow: 1; } }
/* end media min width 1100 pixels */
