.main-section {
  background-color: $colorPrimary;
  color: #fff;

  .container {
    padding-top: 20px;
    padding-bottom: 40px; }

  h1, .title-site {
    margin-bottom: 13px;
    color: #fff; }

  p {
    margin-bottom: 0; }

  p + p {
    margin-top: 20px; } }

.main-sec-title {
  display: flex;
  align-items: center;
  margin-bottom: 18px;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;

  .ico {
    font-size: 19px;
    margin-right: 13px;
    fill: #fff; } }

.question-section {
  padding: 30px 0; }

.operators-section {
  padding: 30px 0 35px; }

.operator-tarif-btns {
  &__item {
    display: flex;
    align-items: flex-end;
    min-height: 104px;
    padding: 24px 17px;
    border: 1px solid $colorPrimary;
    margin-bottom: 5px;
    font-size: 18px;
    line-height: 24px;
    border-radius: 6px;

    &:hover {
      border-color: $colorPrimaryDark; }

    &.opt-btns_img {
      position: relative;
      padding-right: 90px;

      img {
        position: absolute;
        right: 16px;
        bottom: 0;
        width: 73px; } }

    .ico {
      margin-left: 24px; } } }

.review-section {
  padding: 40px 0 30px;

  .st-section-head {
    p {
      max-width: 980px;
      margin-left: auto;
      margin-right: auto; } } }

.main-icons-section {
  padding: 40px 0 20px;

  .mobile-scroll-wrapper {
    margin-bottom: 14px; }

  p {
    margin-bottom: 0; }

  p + p {
    margin-top: 20px; } }

.mobile-scroll-wrapper {
  .main-icons {
    flex-wrap: nowrap;
    justify-content: flex-start;

    li {
      min-width: 125px;
      max-width: 125px;
      width: auto;
      margin: 0 10px 0 0;
      padding-bottom: 14px;

      &.magic-line {
        padding: 0;
        margin: 0;
        min-width: 10px;
        max-width: none; } } } }

.tarif-section {
  padding: 20px 0 30px;

  .st-section-head {
    p {
      margin-bottom: 0; } } }

.tarif-tabs {
  display: flex;
  margin-bottom: 20px;

  li + li {
    margin-left: -1px; }

  li {
    white-space: nowrap;
    border-radius: 0;

    &:first-child {
      border-radius: 24px 0 0 24px; }

    &:last-child {
      border-radius: 0 24px 24px 0; }

    &.current,
    &:hover {
      background-color: $colorPrimary;
      border-color: $colorPrimary;
      color: #fff; }

    &.current {
      cursor: default; } } }

.operator-sim-item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 144px;
  height: 92px;
  padding: 20px;
  border-radius: 13px;
  border: 1px solid #efefef;
  transition: none;

  &:hover {
    border-color: $colorGrayLight;

    &::before {
      background-color: $colorGrayLight;
      background: linear-gradient(-225deg, transparent 18px, $colorGrayLight 0); } }


  &::before {
    content: '';
    position: absolute;
    right: -1px;
    bottom: -1px;
    width: 26px;
    height: 26px;
    z-index: 2;
    background-color: #efefef;
    background: linear-gradient(-225deg, transparent 18px, #efefef 0); }

  &::after {
    content: '';
    position: absolute;
    right: -2px;
    bottom: -2px;
    width: 26px;
    height: 26px;
    z-index: 3;
    background: linear-gradient(-225deg, transparent 18px, #fff 0); } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .main-section {
    overflow: hidden;

    .container {
      position: relative;
      padding: 28px 300px 55px 25px; }

    h1, .title-site {
      margin-bottom: 22px; } }

  .main-section-img {
    position: absolute;
    right: 70px;
    top: 20px;
    width: 220px; }

  .main-sec-title {
    margin-bottom: 4px;
    font-size: 16px;
    line-height: 55px; }

  .question-section {
    padding-bottom: 50px; }

  .operator-tarif-btns {
    display: flex;
    justify-content: center;
    padding: 0 5px;

    &__item {
      display: inline-flex;
      width: 50%;
      padding: 24px 20px;
      margin: 0 5px 5px;
      font-size: 20px;
      line-height: 28px;
      min-height: 114px;
      border-radius: 8px;

      .ico {
        margin-left: 16px;
        font-size: 25px; } } }

  .review-section {
    padding: 45px 0 50px; }

  .main-icons-section {
    padding: 50px 0 40px;

    .mobile-scroll-wrapper {
      margin-bottom: 20px; } }

  .mobile-scroll-wrapper {
    .main-icons {
      li {
        min-width: 172px;
        max-width: 172px;
        padding-bottom: 12px;
        padding-right: 20px; } } }

  .tarif-section {
    padding-top: 40px; }

  .tarif-tabs {
    justify-content: center;
    margin-bottom: 27px; }

  .operator-sim-item {
    width: 172px;
    height: 109px; } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .question-section {
    padding-bottom: 43px;
    padding-top: 65px;

    .st-section-head {
      margin-bottom: 48px; } }

  .operators-section {
    padding: 30px 20px 57px; }

  .operator-tarif-btns {
    max-width: 1000px;
    margin: 0 auto;
    padding: 0;

    &__item {
      padding: 26px 30px;
      margin: 0 10px 5px;
      font-size: 21px;
      line-height: 32px;

      &.opt-btns_img {
        img {
          right: 60px;
          width: 84px; } }

      .ico {
        margin-left: 25px; } } }

  .review-section {
    padding-bottom: 30px; }

  .main-icons-section {
    padding: 58px 0 50px;

    .mobile-scroll-wrapper {
      margin-bottom: 27px; } }

  .tarif-section {
    padding: 50px 0;

    .st-section-head {
      margin-bottom: 35px; } }

  .tarif-tabs {
    margin-bottom: 20px; }

  .operator-sim-item {
    width: 181px;
    height: 115px; } }
/* end media min width 980 pixels */

/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .main-section {
    .container {
      min-height: 440px;
      padding: 43px 500px 40px 25px; }

    h1, .title-site {
      font-size: 35px; }

    p {
      max-width: 650px; } }

  .main-section-img {
    right: 80px;
    width: 290px; }

  .main-sec-title {
    margin-bottom: 0; }

  .mobile-scroll-wrapper {
    .main-icons {
      justify-content: space-between; } } }
/* end media min width 1200 pixels */
