// buttons
button {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  transition: $transDefault; }

.btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: 11px 20px 13px;
  border: none;
  text-align: center;
  font: normal normal 16px/24px $site-font-family, Arial, sans-serif;
  text-decoration: none;
  color: #fff;
  background-color: $colorPrimary;
  border-radius: 24px;
  outline: none;
  cursor: pointer;
  transition: $transDefault;

  &:hover {
    text-decoration: none;
    color: #fff;
    background-color: $colorPrimaryDark; }

  &:active {
    background-color: $colorPrimary; }

  &_border {
    border: 1px solid $colorPrimary;
    color: $colorPrimary;
    background: transparent;
    line-height: 22px;

    &:hover {
      color: #fff;
      border-color: $colorPrimaryDark;
      background-color: $colorPrimaryDark; }

    &:active {
      color: $colorPrimary;
      border-color: $colorPrimary;
      background: transparent; } }

  &_link {
    background-color: transparent;
    color: $colorPrimary;

    &:hover {
      color: $colorPrimaryDark;
      background-color: transparent; } }

  &[disabled] {
    cursor: default;
    color: #fff;
    background-color: $colorGrayLight;
    border-color: $colorGrayLight; }

  &_max {
    width: 100%; } }

.btn-mobile-max-w {
  .btn {
    width: 100%; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .btn-mobile-max-w {
    .btn {
      width: auto; } } }
/* end media min width 740 pixels */
