.burger-menu {
  z-index: 10;
  cursor: pointer;
  padding: 8px;
  user-select: none;
  width: 44px;
  height: 38px;
  margin-left: -8px;
  margin-right: 20px;

  .ico {
    vertical-align: top;
    font-size: 22px;
    fill: $colorPrimary; } }

.mobile-nav {
  margin-top: 30px;
  margin-bottom: 25px; }

.mobile-menu {
  position: fixed;
  top: 0;
  z-index: 30;
  width: 100%;
  height: 100%;
  left: 0;
  max-width: 400px;
  transform: translateX(-100%);
  transition: transform 0.4s ease-in-out;

  .logo {
    display: inline-block;
    vertical-align: middle;
    max-width: 62px; } }

.burger-close {
  display: inline-block;
  vertical-align: middle;
  margin-right: 28px;

  .ico {
    font-size: 18px;
    fill: $colorPrimary; } }

.mobile-menu-inner {
  padding: 12px 12px 30px;
  height: 100%;
  background-color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch; }

.menu-mob {
  li {
    margin: 21px 0;
    font-size: 16px;
    line-height: 21px; }

  &__dd {
    &.opened {
      .menu-open-dd-btn {
        .ico {
          transform: rotate(-180deg); } } }

    .menu-open-dd-btn {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-right: 8px;
      margin-bottom: 20px;
      color: $colorPrimary;

      .ico {
        font-size: 8px;
        stroke: $colorPrimary;
        fill: none;
        transition: $transDefault; } }

    ul {
      display: none;
      margin-left: -12px;
      margin-right: -12px;
      padding: 1px 12px;
      background-color: $colorLight; } } }

.header-mob-contacts {
  margin-bottom: 22px;
  font-size: 18px;
  line-height: 22px;

  .h-work-time {
    margin-bottom: 14px;

    span {
      display: block;
      font-size: 14px; } }

  .h-calls-info {
    span {
      display: block;

      a {
        color: $colorDark; } }

    > a {
      font-size: 14px; } } }

.mobile-btn-order {
  padding-left: 3px;
  padding-right: 3px; }

// opened menu
body {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 0;
    height: 0;
    z-index: 8;
    transition: background-color 0.5s ease-out; } }

.menu_opened {
  overflow: hidden;
  position: fixed;
  height: 100%;

  &:before {
    width: 100%;
    height: 100%;
    background-color: rgba(4, 47, 89, .7); }

  .mobile-menu {
    transform: translateX(0); } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .burger-menu {
    margin-right: 24px; }

  .mobile-menu {
    max-width: 288px; }

  .mobile-menu-inner {
    padding: 58px 26px 38px; }

  .hidden-menu-tablet,
  .header-mob-contacts,
  .mobile-btn-order {
    display: none; }

  .mobile-nav {
    margin: 0; }

  .menu-mob {
    li {
      margin: 24px 0;
      font-size: 18px;
      line-height: 24px; } } }
/* end media min width 740 pixels */

/* Min width 1100 pixels */
@media only screen and (min-width: 1100px) {
  body {
    &:before {
      display: none; } }

  .menu_opened {
    overflow: visible;
    position: relative;

    .site-wrapper {
      height: auto; } }

  .mobile-menu-wrapper {
    display: none; }
  /* end media min width 1100 pixels */ }
