.product-wrapper {
  margin-bottom: 28px; }

.product-head {
  display: flex;
  flex-direction: column;
  margin-bottom: 18px;

  &__logo {
    order: -1;
    margin-bottom: 5px;

    img {
      display: block;
      max-height: 15px; } }

  h1, .title-site_h1 {
    margin-bottom: 5px; }

  .rating-stars {
    &__item {
      height: 21px;
      margin-right: 4px;

      .ico {
        font-size: 21px; } } } }

.product-main {
  margin-bottom: 60px;

  &__view {
    margin-bottom: 36px; } }

.swiper-product {
  .tarif-card {
    overflow: hidden;
    margin-bottom: 0;
    padding: 10px 15px 0 13px; }

  .tarif-item__labels {
    top: 0;
    left: 0; }

  .swiper-nav-wrapper {
    margin-top: 22px; }

  .swiper-pagination-bullet {
    margin: 0 3px; } }

.product-price-bl {
  display: flex;
  align-items: center;
  margin-bottom: 35px;

  &__val {
    margin-right: 30px; }

  &__price {
    font-weight: 700;
    font-size: 32px;
    line-height: 42px;

    span {
      margin-left: 2px; }

    del {
      display: inline-block;
      vertical-align: top;
      margin-top: 4px;
      margin-left: 10px;
      font-weight: normal;
      font-size: 18px;
      line-height: 24px;
      color: $colorDanger; } } }

.prod-service-icons {
  &__ic {
    display: none; }

  > li {
    margin-bottom: 10px;
    font-size: 16px;
    line-height: 21px; }

  strong {
    font-weight: 600; } }

.all-char-link {
  margin: 30px 0 40px;
  text-align: center; }

.product-more-info {
  .simple-tabs {
    display: flex;
    margin-bottom: 20px;
    padding-bottom: 11px;
    border-bottom: 1px solid $colorLight;

    li {
      width: 125px;
      margin-right: 10px;
      padding-top: 20px;
      font-weight: 700;
      font-size: 12px;
      line-height: 20px;
      text-transform: uppercase; } }

  .form-review {
    margin-top: 25px;
    padding-top: 20px;
    border-top: 1px solid $colorGrayLight; }

  .table-flex-mobile {
    margin-top: 30px; } }

.product-reviews {
  &__head {
    display: flex;
    align-items: center;

    strong {
      margin-right: 12px; } }

  strong {
    font-weight: 600;
    font-size: 14px;
    line-height: 36px; }

  &__answer {
    min-height: 62px;
    margin-left: 8px;
    margin-top: 30px;
    padding-left: 11px;
    border-left: 1px solid $colorGrayLight; }

  > li {
    margin-bottom: 25px; }
  p {
    font-size: 16px;
    line-height: 21px;
    margin-bottom: 10px; } }

.product-more-reviews-link {
  text-align: center; }

.product-fix-buy {
  height: 80px; }

.product-fix-buy-inner {
  padding: 15px 0;
  border-top: 1px solid $colorGrayLight;
  background-color: #fff; }

.pf-buy-title,
.pf-buy-price {
  display: none; }

.pf-buy-flex {
  display: flex;
  align-items: center;
  justify-content: space-between;

  .btn {
    flex-grow: 1;
    margin-right: 20px; } }

.btn-top {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border: 1px solid $colorPrimary;
  border-radius: 50%;
  transition: $transDefault;

  &:hover {
    background-color: $colorPrimary;

    .ico {
      fill: #fff; } }

  .ico {
    font-size: 10px;
    fill: $colorPrimary;
    transform: $transDefault; } }

.sticky-prod {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 40; }

.product-fix-buy-inner {
  &.animated {
    animation: slideUp .4s ease; } }

@keyframes slideUp {
  from {
    transform: translate3d(0, 100%, 0); }
  to {
    transform: translate3d(0, 0, 0); } }

.card_product {
  .product-head {
    display: block;

    h1, .title-site_h1 {
      margin-right: 10px;
      display: inline; }

    .rating-stars {
      display: inline-flex;
      margin-top: 4px;

      &__item {
        margin-right: 4px; } } }

  .tarif-item__img {
    margin-top: 17px;
    height: 174px;
    text-align: center;

    img {
      max-height: 100%;
      width: auto; } }

  .tarif-item__labels {
    left: auto;
    right: 0; }

  .tarif-label {
    margin-right: 0;
    margin-left: 5px;
    padding: 5px 9px 7px;
    font-size: 16px;
    line-height: 21px; } }

.product-descipt {
  p {
    margin-bottom: 20px; }

  ol {
    margin-top: 5px;
    margin-bottom: 20px;
    counter-reset: countNum;

    li {
      position: relative;
      padding-left: 16px;

      &::before {
        counter-increment: countNum;
        content: counter(countNum) ".";
        position: absolute;
        left: 0;
        top: 0;
        color: $colorGray; } } } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .product-wrapper {
    margin-bottom: 20px; }

  .product-head {
    position: relative;
    display: block;
    margin-bottom: 43px;

    &_with-logo {
      padding-right: 180px; }

    &__logo {
      position: absolute;
      right: 0;
      top: 10px;
      width: 144px;
      margin: 0;

      img {
        max-height: 20px; } }

    h1, .title-site_h1 {
      display: inline;
      margin-bottom: 0;
      margin-right: 8px; }

    .rating-stars {
      display: inline-flex;
      margin-top: 6px;

      &__item {
        height: 25px;
        margin-right: 5px;

        .ico {
          font-size: 25px; } } } }

  .product-main {
    display: flex;

    &__view {
      min-width: 263px;
      max-width: 263px;
      margin-right: 35px; }

    &__about {
      padding-top: 20px; } }

  .product-main_tarif {
    .product-main__about {
      padding-top: 10px; } }

  .swiper-product {
    .tarif-card {
      padding-right: 0; }

    .swiper-nav-wrapper {
      margin-top: 42px; }

    .swiper-pagination-bullet {
      margin: 0 6px; } }

  .prod-service-icons {
    > li {
      position: relative;
      padding-left: 57px;
      margin-bottom: 20px; }

    &__ic {
      position: absolute;
      left: 0;
      top: -3px;
      display: block;

      .ico {
        font-size: 26px;
        fill: $colorPrimary; }

      .ico-mono-list-ruble {
        margin-left: 8px; }

      .ico-mono-list-arrows {
        top: -4px;
        font-size: 27px; }

      .ico-mono-list-devices {
        margin-left: 5px;
        font-size: 25px; }

      .ico-mono-list-round {
        margin-left: 11px;
        font-size: 25px; } } }

  .all-char-link {
    margin: 30px 0;
    text-align: left; }

  .product-price-bl {
    margin-bottom: 30px;

    &__val {
      margin-right: 44px; } }

  .product-more-info {
    .simple-tabs {
      padding-bottom: 12px;

      li {
        font-size: 14px;
        width: 172px; } }

    .form-review {
      padding-top: 30px; }

    .table-flex-mobile {
      margin-bottom: 40px; } }

  .product-reviews {
    &__answer {
      margin-left: 19px; }

    > li {
      margin-bottom: 32px;

      &:last-child {
        margin-bottom: 25px; } } }

  .product-more-reviews-link {
    text-align: left; }

  .product-fix-buy {
    height: 82px; }

  .product-fix-buy-inner {
    padding-top: 17px; }

  .pf-buy-flex {
    .btn {
      flex-grow: 0;
      margin-right: 11px;
      margin-left: auto;
      white-space: nowrap; } }

  .pf-buy-price {
    min-width: 130px;
    margin: 0 48px 0 38px;
    display: block;
    white-space: nowrap;
    text-align: center;

    .pf-buy-price__val {
      display: none; }

    &__price {
      font-weight: 700;
      font-size: 18px;
      line-height: 42px;

      span {
        margin-left: 2px; }

      del {
        display: inline-block;
        vertical-align: top;
        margin-left: 8px;
        margin-top: 4px;
        font-weight: normal;
        font-size: 14px;
        line-height: 24px;
        color: $colorDanger; } } }

  .pf-buy-title {
    display: block;
    flex-grow: 1;
    font-size: 16px;
    line-height: 24px; } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .product-wrapper {
    margin-bottom: 40px; }

  .product-head {
    margin-bottom: 72px;

    &_width-logo {
      padding-right: 200px; }

    &__logo {
      width: 180px;
      top: 15px; } }

  .product-main {
    margin-bottom: 40px;

    &__view {
      min-width: 380px;
      max-width: 380px;
      margin-right: 118px; } }

  .swiper-product {
    .tarif-card {
      padding: 10px 0 0; } }

  .prod-service-icons {
    > li {
      margin-bottom: 28px;
      font-size: 18px;
      line-height: 24px; } }

  .all-char-link {
    margin-bottom: 40px; }

  .product-price-bl {
    margin-bottom: 33px;

    &_val {
      margin-right: 30px; } }

  .product-more-info {
    .simple-tabs {
      margin-bottom: 33px;

      li {
        margin-right: 20px;
        line-height: 18px;
        width: 180px; } }

    .form-review {
      margin-top: 34px;
      padding-top: 40px; } }

  .product-reviews {
    &__head {
      strong {
        margin-right: 15px; } }

    strong {
      font-size: 18px; }

    p {
      font-size: 18px;
      line-height: 24px; }

    &__answer {
      margin-top: 25px;
      margin-left: 40px;
      padding-left: 19px;
      padding-top: 10px;
      padding-bottom: 10px;
      font-size: 18px;
      line-height: 28px;

      p {
        line-height: 28px;

        &:last-child {
          margin-bottom: 0; } } }

    > li {
      margin-bottom: 40px;

      &:last-child {
        margin-bottom: 50px; } } }

  .product-more-reviews-link {
    text-align: left;
    font-size: 18px;
    line-height: 28px; }

  .pf-buy-flex {
    .btn {
      margin-right: 30px; } }

  .pf-buy-title {
    font-size: 18px;
    line-height: 32px; }

  .pf-buy-price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width: 240px;
    margin: 0 60px 0 20px;

    .pf-buy-price__val {
      display: block; }

    &__price {
      font-size: 21px;
      line-height: 32px;

      span {
        margin-left: 3px; }

      del {
        margin-top: 1px; } } }

  .card_product {
    .product-head {
      margin-bottom: 30px; }

    .tarif-item__img {
      margin-top: 21px;
      height: 264px; }

    .tarif-label {
      padding: 10px 20px 12px; } }

  .product-descipt {
    p {
      margin-bottom: 28px; }

    ol {
      margin-bottom: 28px;

      li {
        padding-left: 18px; } } } }
/* end media min width 980 pixels */

/* Min width 1180 pixels */
@media only screen and (min-width: 1180px) {
  .pf-buy-price {
    min-width: 290px;
    margin-right: 130px; } }
/* end media min width 1180 pixels */
