.calc-wrapper {
  margin-bottom: 5px; }

.calc-form {
  margin: 25px 0 0;

  .btn-mobile-max-w {
    margin-top: -10px; } }

.calc-item {
  margin-bottom: 30px;
  padding-bottom: 12px;
  border-bottom: 1px solid $colorGrayLight;

  &__tt {
    margin-bottom: 15px;
    font-size: 18px;
    line-height: 24px; } }

.form-controls-list {
  display: flex;
  flex-wrap: wrap;

  li {
    flex: 0 0 50%;
    padding-right: 5px;
    margin-bottom: 3px; }

  &_max-mob-width {
    li {
      flex: 0 0 100%; } } }

.range-value {
  margin-top: 34px;
  font-size: 32px;
  line-height: 42px;
  font-weight: 700;
  text-align: center; }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .calc-wrapper {
    margin-bottom: 20px; }

  .calc-form {
    margin: 40px 0 0;

    .btn-mobile-max-w {
      margin-top: 0; } }

  .calc-item {
    &__tt {
      margin-bottom: 32px;
      font-size: 20px;
      line-height: 28px; } }

  .form-controls-list {
    li {
      flex: 0 0 25%;
      margin-bottom: 10px; }

    &_max-mob-width {
      li {
        flex: 0 0 50%; } } }

  .range-and-checkboxes-wrapper {
    .form-controls-list {
      visibility: hidden;
      width: 0;
      height: 0; } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .calc-form {
    margin: 54px 0 0; }

  .calc-item {
    &__tt {
      margin-bottom: 15px;
      font-size: 21px;
      line-height: 32px; } } }
/* end media min width 980 pixels */

/* Min width 1100 pixels */
@media only screen and (min-width: 1100px) {
  .form-controls-list {
    li {
      flex: 0 0 16.5%;
      padding-right: 10px; }

    &_max-mob-width {
      li {
        flex: 0 0 50%;
        padding-right: 20px; } } } }
/* end media min width 1100 pixels */

