.mfp-wrap {
  transition: $transDefault;
  overflow: auto; }

.mfp-bg {
  background: rgba(4, 47, 89, .7); }

.mfp-bg.mfp-removing {
  opacity: 0; }

.mfp-container {
  padding: 0; }

.mfp-content {
  padding: 21px 12px; }

.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  padding: 0;
  right: 0;
  width: 35px; }

button.mfp-close,
.mfp-close,
button.mfp-arrow {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
  text-indent: -9999px;
  color: $colorPrimary;
  background-image: url('../img/close.svg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 17px;
  transition: $transDefault;

  &::before {
    display: none; } }

button.mfp-close,
.mfp-close {
  top: 13px;
  right: 10px;
  width: 35px;
  height: 35px;
  opacity: 1;

  &:active {
    top: 13px; } }

button.mfp-arrow {
  background-color: #000;
  padding-right: 5px;

  &::after {
    display: none; }

  &.mfp-arrow-right {
    padding: 0 0 0 5px;

    .icon {
      transform: rotate(-180deg); } }

  .icon {
    width: 30px;
    height: 30px;
    fill: #fff; } }

// magnific gallery
.mfp-zoom-out-cur {
  cursor: default; }

.mfp-zoom-out-cur .mfp-image-holder .mfp-close {
  cursor: pointer; }

.magnific-gallery {
  button {
    margin-right: 5px;
    height: 100px;

    img {
      max-height: 100%; } } }

.mfp-figure {
  position: relative;

  &::after {
    top: 0;
    bottom: 0;
    background-color: rgba(#000, .3);
    box-shadow: 0 0 6px rgba(#000, .3); }

  img.mfp-img {
    padding: 0; } }

.mfp-bottom-bar {
  display: flex;
  justify-content: space-between;
  margin-top: 0;
  padding: 5px; }

.mfp-title {
  margin-right: 10px;
  font-size: 14px;
  line-height: 24px; }

.mfp-counter {
  position: relative;
  font-size: 14px;
  line-height: 24px; }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .mfp-content {
    padding: 44px 25px; }

  button.mfp-close,
  .mfp-close {
    top: 28px;
    right: 12px;

    &:active {
      top: 28px; } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  button.mfp-close,
  .mfp-close {
    top: 25px;
    right: 20px;

    &:hover {
      transform: scale(1.2); }

    &:active {
      top: 25px; } } }
/* end media min width 980 pixels */
