.delivery-wrapper {
  .ol-list-num {
    li:before {
      top: 3px; } }

  h2, .title-site {
    margin-bottom: 14px; } }

.delivery-col-main {
  margin-bottom: 10px; }

.delivery-val-inline {
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
  width: 21px;
  height: 21px;
  line-height: 20px;
  color: #fff;
  text-align: center;
  background-color: $colorDanger;
  border-radius: 50%; }

.del-table-wrapp {
  .table-flex-mobile {
    margin-bottom: 20px;

    tr {
      padding-left: 0;
      padding-right: 0;
      border-width: 2px;

      &:first-child {
        border-top: 0;
        padding-top: 0; } } } }

.del-tbl-min {
  font-size: 14px;
  line-height: 18px; }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .delivery-wrapper {
    .ol-list-num {
      li {
        margin-bottom: 40px;

        &:before {
          top: 5px; } } }

    h2, .title-site {
      margin-bottom: 20px; } }

  .delivery-col-main {
    margin-bottom: 15px; }

  .delivery-val-inline {
    font-size: 16px;
    width: 26px;
    height: 26px;
    line-height: 25px; }

  .del-table-wrapp {
    .table-flex-mobile {
      margin-bottom: 14px;

      tr {
        &:first-child {
          border-top: 0;
          padding-top: 0;

          td {
            padding-top: 0;
            border-top: 0; } } }

      td {
        border: none;
        border-bottom: 2px solid $colorGrayLight; } } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .delivery-wrapper {
    .ol-list-num {
      li {
        &:before {
          top: 7px; } } }

    h2, .title-site {
      margin-bottom: 25px; } }

  .delivery-row-adress {
    display: flex;
    justify-content: space-between; }

  .delivery-col-main {
    margin-bottom: 0;
    max-width: 720px;
    margin-right: 40px; }

  .delivery-val-inline {
    font-size: 18px;
    width: 36px;
    height: 36px;
    line-height: 35px;
    margin-right: 18px; }

  .del-table-wrapp {
    .table-flex-mobile {
      tr {
        &:first-child {
          td {
            padding-top: 0;
            border-top: 0; } } }

      td {
        border: none;
        border-bottom: 2px solid $colorGrayLight; } } } }
/* end media min width 980 pixels */
