.st-modal {
  max-width: 978px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  padding-bottom: 20px;
  background-color: #fff;
  border-radius: 20px;

  &__head {
    padding: 14px 20px 15px;
    padding-right: 40px;
    border-radius: 20px 20px 0 0;

    .title-site, h2 {
      margin-bottom: 0; } }

  &__body {
    padding: 15px 20px 20px;
    border-radius: 0 0 20px 20px; }

  .st-form {}

  .form-confirm-text {
    margin: 30px 0; }

  .btn {
    width: auto; } }

.invalid-form {
  .form-invalid-info {
    display: block; } }

.form-invalid-info {
  display: none;
  margin-top: 20px;
  font-size: 14px;
  line-height: 18px; }

#modalSuccess {
  .st-modal {
    &__head {
      padding: 25px 25px 32px;
      background-color: $colorLight;
      text-align: center; }

    &__body {
      padding: 26px 20px 20px;

      .title-site {
        margin-bottom: 55px; } } } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .st-modal {
    &__head {
      padding: 27px 60px 26px 20px; }

    &__body {
      padding: 27px 20px 20px; }

    .form-three-col {
      flex-direction: column; }

    .field-item {
      width: 100%;
      margin-bottom: 40px; }

    .form-confirm-text {
      margin-top: 0;
      margin-bottom: 20px; } }

  #modalSuccess {
    .st-modal {
      &__head {
        padding: 65px 25px 80px; }

      &__body {
        padding: 50px 20px 40px;

        .title-site {
          margin-bottom: 38px; } } } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .st-modal {
    &__head {
      padding: 23px 60px 25px 30px; }

    &__body {
      padding: 25px 30px 20px; }

    .form-three-col {
      flex-direction: row;

      .field-item {
        margin-bottom: 40px;
        width: 32%; } }

    .field-item {
      margin-bottom: 30px; } }

  #modalSuccess {
    .st-modal {
      &__head {
        padding: 50px 30px; }

      &__body {
        padding: 43px 30px 40px;

        .title-site {
          margin-bottom: 55px; } } } } }
/* end media min width 980 pixels */
