* {
  box-sizing: border-box;
  outline: none; }

html {
  height: 100%; }

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  max-width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
  background-color: #fff;
  font: $site-text-size $site-font-family, $systemFont, sans-serif;
  line-height: 21px;
  color: $colorDark;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

::selection {
  color: white;
  background: $colorPrimary; }

body.document-scroll-blocker {
  touch-action: none;
  overscroll-behavior: none;
  -webkit-overflow-scrolling: auto;
  overflow: hidden; }

.site-wrapper {
  overflow: hidden;
  flex: 1 0 auto; }

.footer {
  flex-shrink: 0; }

.section-st {
  padding-top: 80px;
  padding-bottom: 20px; }

.section-d-bg {
  background-color: $colorLight;

  .btn {
    background-color: #fff; } }

.container {
  padding-left: 12px;
  padding-right: 12px; }

.hidden-mobile,
.hidden-devices {
  display: none; }

.d-flex-ai-center-space-b {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.border-bottom-section {
  border-bottom: 1px solid $colorGrayLight; }

.st-section-head {
  text-align: center;

  .title-site, h2 {
    margin-bottom: 15px; }

  p {
    margin-bottom: 20px; } }

.mobile-scroll-wrapper {
  margin-left: -12px;
  margin-right: -12px;
  padding-left: 12px;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;

  &.m-scroll-bottom-line {
    position: relative;
    box-shadow: 0 -1px 0 inset $colorGrayLight;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 12px;
      height: 1px;
      z-index: 2;
      background-color: #fff; } } }

.mobile-scroll-wrapper::-webkit-scrollbar {
  display: none;
  height: 0;
  width: 0; }

.inner-content {
  padding: 30px 0; }

.inner-top-line {
  padding: 20px 0 30px;
  border-top: 1px solid $colorGrayLight; }

.inner-content-head {
  h1 {
    margin-bottom: 20px; } }

.inner-banner {
  margin-bottom: 20px;
  border-radius: 4px;
  overflow: hidden;

  picture, img {
    width: 100%; } }

.not-found-page {
  padding: 10px 0;
  text-align: center;

  img {
    width: 100%;
    max-width: 536px; }

  .title-site, h1 {
    margin: 20px 0; }

  p {
    margin-bottom: 0; } }

.more-products-section {
  margin-bottom: 52px;

  .title-site_h2, h2 {
    margin-bottom: 15px;
    line-height: 32px; } }

.more-products-section + .more-products-section {
  margin-top: 87px; }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .hidden-tablet {
    display: none; }

  .hidden-mobile {
    display: block; }

  .inner-top-line {
    padding-bottom: 45px; }

  .container {
    padding-left: 25px;
    padding-right: 25px; }

  .st-section-head {
    p {
      margin-bottom: 25px; } }

  .mobile-scroll-wrapper {
    margin-left: -25px;
    margin-right: -25px;
    padding-left: 25px;

    &.m-scroll-bottom-line {
      &::before {
        width: 25px; } } }

  .inner-banner {
    margin-bottom: 30px;
    border-radius: 8px; }

  .not-found-page {
    max-width: 536px;
    margin: 0 auto;
    padding: 20px 0;

    .title-site, h1 {
      margin: 35px 0 25px; } }

  .more-products-section {
    margin-bottom: 50px;

    .title-site_h2, h2 {
      font-size: 26px; } }

  .more-products-section + .more-products-section {
    margin-top: 0; } }
/* end media min width 740 pixels */


/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  body.document-scroll-blocker {
    &.menu_opened {
      touch-action: auto;
      overscroll-behavior: auto;
      overflow: visible; } }

  .container {
    max-width: 1230px;
    margin-left: auto;
    margin-right: auto; }

  .hidden-devices,
  .hidden-tablet {
    display: block; }

  .hidden-desktop {
    display: none; }

  .inner-top-line {
    padding-bottom: 60px; }

  .st-section-head {
    margin-bottom: 30px;

    .title-site, h2 {
      margin-bottom: 20px; } }

  .inner-content-head {
    h1 {
      margin-bottom: 32px; } }

  .inner-banner {
    margin-bottom: 60px; }

  .not-found-page {
    padding: 10px 0 30px;
    max-width: 580px;

    img {
      max-width: 480px; }

    .title-site, h1 {
      margin: 45px 0 25px; } }

  .more-products-section {
    margin-bottom: 80px;

    .title-site_h2, h2 {
      margin-bottom: 24px;
      font-size: 32px;
      line-height: 42px; } }

  .more-products-section + .more-products-section {
    margin-top: 112px; } }
/* end media min width 980 pixels */

/* Min width 1100 pixels */
@media only screen and (min-width: 1100px) {
  .mobile-scroll-wrapper {
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;

    &.m-scroll-bottom-line {
      &::before {
        display: none; } } } }
/* end media min width 1100 pixels */
