.field-item {
  margin-bottom: 20px; }

.form-confirm-text {
  margin-bottom: 16px; }

.form-btn {
  .btn {
    width: 100%; } }

.form-review {
  .rating-stars {
    &__item {
      height: 25px;
      margin-right: 5px;

      .ico {
        font-size: 25px; } } } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .form-three-col {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .field-item {
      width: 49%; } }

  .form-confirm-text {
    margin-top: 30px;
    margin-bottom: 36px; }

  .form-btn {
    .btn {
      width: auto; } }

  .form-review {
    .rating-stars {
      &__tt {
        line-height: 40px; } }

    .form-btn {
      margin-top: 26px; } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .form-three-col {
    .field-item {
      width: 32%;
      max-width: 380px;
      margin-bottom: 42px; } }

  .form-confirm-text {
    margin-top: 28px;
    margin-bottom: 19px;
    line-height: 28px; }

  .form-review {
    .rating-stars {
      &__tt {
        line-height: 60px;
        margin-right: 20px; } }

    .form-btn {
      margin-top: 45px; } } }
/* end media min width 980 pixels */
