/* UI */
.form-group {
  margin-bottom: 30px; }

.field-item {
  &__label {
    margin-bottom: 10px;
    font-size: 16px;
    line-height: 20px;
    color: #0B3954; } }

.placeholder {
  color: #37393A; }

input,
textarea {
  @include placeholder-color($colorGray); }

::-webkit-contacts-auto-fill-button {
  opacity: 0; }

input[type="password"]::-webkit-contacts-auto-fill-button {
  opacity: 1; }

input[type="search"] {
  -webkit-appearance: none; }

.field-item__field {
  input,
  textarea {
    position: relative;
    height: 50px;
    width: 100%;
    padding: 0 20px;
    border: 1px solid #D5D5D5;
    font: 16px $site-font-family, Arial, sans-serif;
    color: $colorDark;
    background-color: #fff;
    outline: none;
    border-radius: 2px;
    -webkit-appearance: none;
    box-shadow: none;
    transition: $transDefault;

    &:focus,
    &:required:valid {
      border-color: $colorDark; } }

  label.error {
    margin-top: 3px;
    font-size: 14px;
    line-height: 18px;
    color: $colorDark; } }

.field-item__field textarea {
  vertical-align: top;
  height: 80px;
  padding: 10px 20px;
  line-height: 28px;
  resize: none; }

.field-item__field input.error,
.field-item__field textarea.error,
input.error {
  border-color: $colorDanger; }


/* checkbox and radio */
.form-st-radio,
.form-st-checkbox {
  position: relative;
  padding-left: 33px;
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 10px;

  label {
    cursor: pointer; }

  input {
    width: 0;
    height: 0;
    visibility: hidden;
    position: absolute;
    left: 0;
    top: 0; } }

.fake-control {
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  background-color: #fff;
  box-shadow: 0 0 0 1px $colorGray;
  border-radius: 4px; }

.form-st-radio {
  .fake-control {
    border-radius: 50%;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 0;
      height: 0;
      visibility: hidden;
      background-color: $colorPrimaryDark;
      border-radius: 50%;
      transition: $transDefault; } }

  input:checked + .fake-control {
    &::before {
      visibility: visible;
      width: 10px;
      height: 10px;
      margin: -5px 0 0 -5px;
      box-shadow: none; } } }

.form-st-checkbox {
  input:checked + .fake-control {
    background-color: $colorPrimaryDark;
    background-image: url('../img/check-white.svg');
    background-repeat: no-repeat;
    background-position: 3px 50%;
    background-size: 13px auto;
    transition: background-color $transDefault;
    box-shadow: 0 0 0 1px $colorPrimaryDark; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .field-item__field input,
  .field-item__field textarea {
    height: 60px;
    font-size: 18px;
    border-radius: 4px; }

  .field-item__field textarea {
    height: 100px;
    padding-top: 16px; } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .form-st-radio,
  .form-st-checkbox {
    font-size: 18px; } }
/* end media min width 980 pixels */
