// FOOTER
.footer-flex-inner {
  padding: 19px 0 27px;
  border-top: 2px solid $colorLight;

  .logo {
    display: none; } }

.footer-nav {
  margin-bottom: 32px;
  font-size: 16px;
  line-height: 21px;
  text-align: center;

  li {
    display: inline-block;
    vertical-align: top;
    margin: 0 12px;

    &:first-child {
      margin-left: 0; }

    &:last-child {
      margin-right: 0; } } }

.footer-search {
  margin-bottom: 25px; }

.footer-phones {
  display: inline-block;
  vertical-align: top;
  margin-right: 28px;
  font-size: 18px;
  line-height: 22px;

  span {
    display: block;

    a {
      color: $colorDark;

      &:hover {
        color: $colorPrimaryDark; } } }

  > a {
    font-size: 14px; } }

.footer-rating-wrapp {
  display: inline-block;
  vertical-align: top;
  margin-left: auto;
  width: 120px;
  height: 110px;
  margin-bottom: 20px;
  overflow: hidden;

  img {
    vertical-align: top; } }

.footer-adress {
  flex: 100%;
  font-size: 16px;
  line-height: 22px;
  text-align: center; }

.footer-bottom-info {
  text-align: center;
  padding: 14px 0 33px;
  background-color: #f2f2f2; }

.soc-inline-round {
  margin-bottom: 25px;

  li {
    display: inline-block;
    vertical-align: top;
    margin: 0 4px; }

  a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    border: 1px solid $colorPrimary;
    transition: $transDefault;

    &:hover {
      background-color: $colorPrimary;

      .ico {
        fill: #ffffff; } } }

  .ico-mono-insta {
    font-size: 24px; }

  .ico-mono-fb {
    font-size: 25px; } }

.copy {
  display: block;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 33px; }

.paymant-inline {
  li {
    display: inline-block;
    vertical-align: middle;
    margin: 0 8px;

    img {
      vertical-align: middle; } } }

.footer-contacts {
  display: flex;
  flex-wrap: wrap; }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .footer-flex-inner {
    padding: 40px 0 55px;

    .logo {
      display: block; } }

  .footer-nav {
    margin-bottom: 27px;
    text-align: left; }

  .footer-search-wrapp {
    display: flex;
    align-items: center;
    margin-bottom: 40px; }

  .footer-search {
    flex-grow: 1;
    margin-bottom: 0;
    margin-left: 58px; }

  .footer-contacts {
    flex-wrap: nowrap;
    align-items: flex-start; }

  .footer-phones {
    padding-left: 25px;
    margin-bottom: 0;
    border-left: 1px solid $colorGrayLight;
    padding: 6px 25px;
    order: 2; }

  .footer-rating-wrapp {
    order: 3;
    margin-left: auto;
    margin-bottom: 0; }

  .footer-adress {
    padding: 6px 0;
    max-width: 290px;
    margin-right: 25px;
    font-size: 18px;
    line-height: 24px;
    text-align: left; }

  .footer-bottom-info {
    padding: 15px 0 16px;
    text-align: left;

    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%; } }

  .soc-inline-round {
    order: 2;
    margin: 0;

    li {
      margin: 0 0 0 7px; } }

  .copy {
    margin: 0;
    font-size: 18px;
    line-height: 22px; } }
/* end media min width 740 pixels */

/* Min width 1100 pixels */
@media only screen and (min-width: 1100px) {
  .footer-flex-inner {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 0 34px; }

  .footer-nav {
    order: 1;
    margin-bottom: 0;
    margin-right: auto;
    padding-top: 6px;
    font-size: 18px;
    line-height: 24px;

    li {
      margin: 0 14px; } }

  .footer-search-wrapp {
    margin-bottom: 45px;
    width: 100%; }

  .footer-search {
    margin-left: 130px; }

  .footer-contacts {
    display: flex;
    order: 2; }

  .footer-rating-wrapp {
    margin-top: -15px; } }
/* end media min width 1100 pixels */

/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .footer-rating-wrapp {
    margin-left: 60px; } }
/* end media min width 1200 pixels */
