.tarif-list {
  margin-bottom: 20px;

  .tarif-item {
    margin-bottom: 10px; } }

.tarif-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 350px;
  margin: 0 auto;
  border: 1px solid #efefef;
  background-color: #fff;
  border-radius: 20px;

  &__devices-list {
    display: flex;
    align-items: center;
    padding: 3px 13px 6px;
    margin-bottom: 15px;
    font-size: 14px;
    line-height: 21px;
    background-color: $colorLight;
    border-radius: 15px; }

  &__img {
    display: block;
    overflow: hidden;
    border-radius: 14px;

    &:hover {
      img {
        opacity: .95; } }

    img {
      width: 100%; } }

  &__labels {
    position: absolute;
    left: -5px;
    top: -7px;
    display: flex;
    z-index: 2; }

  &__title {
    display: inline-block;
    vertical-align: top;
    margin-bottom: 15px;
    font-size: 20px;
    line-height: 26px; }

  &__list-info {
    li {
      position: relative;
      padding-left: 16px;
      font-size: 16px;
      line-height: 21px;
      margin-bottom: 8px;

      &::before {
        content: '';
        position: absolute;
        left: 1px;
        top: 0;
        color: $colorGray; } } }

  &__price {
    display: flex;
    margin-bottom: 25px;
    font-weight: 700;
    font-size: 22px;
    line-height: 36px;

    del {
      margin-left: 10px;
      font-family: $site-font-family, Arial, sans-serif;
      font-weight: normal;
      font-size: 18px;
      line-height: 24px;
      color: $colorDanger; } }

  &__add {
    .btn {
      width: 100%; } } }

.tarif-card {
  position: relative;
  margin-bottom: 10px; }

.tarif-label {
  display: inline-flex;
  align-items: center;
  margin-right: 5px;
  padding: 1px 5px 3px;
  font-size: 10px;
  line-height: 21px;
  background-color: $colorLight;
  border-radius: 6px;
  white-space: nowrap;

  &_action {
    background-color: $colorDanger;
    color: #fff; }

  .ico {
    font-size: 12px;
    margin-right: 3px;
    fill: #1EAF24; } }

.tarif-item-body {
  padding: 17px 15px 30px;

  p {
    font-size: 16px;
    line-height: 21px; } }

.tarif-item-bottom {
  padding: 0 15px 24px; }

.devices-list-icons {
  display: inline-flex;
  align-items: center;
  margin-left: 5px;

  li {
    position: relative;
    height: 15px;
    margin: 0 2px;

    .ico {
      font-size: 15px; }

    .lock-d-list {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      right: -6px;
      top: 0;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: #fff;
      box-shadow: 0px 4px 4px rgba(#000, .25);

      .ico {
        font-size: 6px; } } } }

.tarif-item_product {
  .tarif-item-body {
    padding-top: 20px;
    padding-bottom: 15px;

    p {
      margin-bottom: 10px; } }

  .tarif-card {
    margin-bottom: 35px; }

  .tarif-item__img {
    height: 144px;
    line-height: 144px;
    text-align: center;

    img {
      width: auto; } } }

.catalog-col-aside {
  margin-bottom: 40px; }

.catalog-list-pagin {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;

  &__count {
    width: 107px;
    font-size: 18px;
    line-height: 24px;
    text-align: center; }

  &__prev,
  &__next {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 1px solid #3099FF;
    transition: $transDefault;

    &:hover {
      background-color: $colorPrimary;

      svg {
        fill: #fff; } }

    svg {
      transition: $transDefault;
      fill: $colorPrimary; } }


  &__prev {
    padding-right: 1px; }

  &__next {
    padding-left: 1px; } }

.prod-nav-panel-more {
  margin-bottom: 30px;
  padding: 22px 20px 20px;
  background-color: $colorLight;
  border-radius: 8px;

  &__tt {
    margin-bottom: 4px;
    font-size: 18px;
    line-height: 24px; }

  &__links {
    margin: 0;
    padding: 0;

    li {
      margin: 0;
      font-size: 18px;
      line-height: 32px; } } }


/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .tarif-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .tarif-item {
      width: 49.3%;
      max-width: 100%;
      margin: 0 0 10px; } }

  .tarif-item {
    &__add {
      .btn {
        width: auto; } } }

  .tarif-item-bottom {
    padding-bottom: 30px; }

  .tarif-item_product {
    .tarif-card {
      margin-bottom: 23px; }

    .tarif-item__title {
      margin-bottom: 22px;
      font-size: 22px;
      line-height: 30px; } }

  .catalog-row {
    display: flex; }

  .catalog-col-aside {
    flex: 0 0 172px;
    margin-right: 10px;
    margin-bottom: 20px; }

  .catalog-col {
    flex: 1; }

  .catalog-list-pagin {
    margin-bottom: 20px; }

  .prod-nav-panel-more {
    display: flex;
    padding: 20px;
    margin-bottom: 40px;

    &__tt {
      flex: 0 0 265px;
      margin-right: 20px;
      font-size: 20px;
      line-height: 28px; }

    &__links {
      display: flex;
      flex-wrap: wrap;

      li {
        width: 50%;
        padding-right: 10px;
        font-size: 21px; } } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .tarif-item {
    &__title {
      font-size: 22px;
      line-height: 30px; } }

  .tarif-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .tarif-item {
      width: 32%;
      margin-bottom: 50px; } }

  .devices-list-icons {
    li {
      .ico {
        font-size: 20px; }

      .lock-d-list {
        right: -8px;
        width: 12px;
        height: 12px;

        .ico {
          font-size: 9px; } } } }

  .tarif-item_product {
    .tarif-item-body {
      padding-top: 20px;
      padding-bottom: 15px;

      p {
        margin-bottom: 10px; } }

    .tarif-card {
      margin-bottom: 6px; }

    .tarif-item__img {
      height: 161px;
      line-height: 161px; } }

  .catalog-row {
    padding-top: 15px; }

  .tarif-list {
    margin-bottom: 0; }

  .catalog-list-pagin {
    margin-top: -20px; }

  .prod-nav-panel-more {
    min-height: 100px;
    align-items: center;

    &__tt {
      margin-bottom: 0;
      margin-right: 40px; }

    &__links {
      flex: 1;

      li {
        width: 25%;
        padding-right: 10px; } } } }
/* end media min width 980 pixels */

/* Min width 1100 pixels */
@media only screen and (min-width: 1100px) {
  .catalog-col-aside {
    flex: 0 0 280px;
    margin-right: 20px; } }
/* end media min width 1100 pixels */
