.site-map-catalog {
  font-size: 18px;
  line-height: 24px;
  color: $colorPrimary;

  ul {
    margin: 10px;
    font-size: 16px;
    line-height: 21px;

    li {
      &::before {
        content: '';
        margin-right: 3px;
        color: $colorPrimary; } } } }

.site-map-list {
  li {
    font-size: 18px;
    line-height: 35px; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .site-map {
    display: flex;
    margin-top: 25px; }

  .site-map-catalog {
    width: 50%;
    font-size: 20px;
    line-height: 28px;

    ul {
      margin: 24px 10px 0; } }

  .site-map-list {
    width: 50%;

    li {
      display: inline-block;
      vertical-align: top;
      font-size: 20px;
      line-height: 38px;
      margin-right: 25px; } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .site-map {
    display: flex;
    margin-top: 40px;
    margin-bottom: 20px; }

  .site-map-catalog {
    width: auto;
    min-width: 340px;
    max-width: 340px;
    margin-right: 25px;
    font-size: 21px;
    line-height: 32px;

    ul {
      margin: 13px 0 0;
      font-size: 18px;
      line-height: 24px; } }

  .site-map-list {
    flex-grow: 1;

    li {
      margin: 0 0 0 25px;
      font-size: 21px;
      line-height: 42px; } } }
/* end media min width 980 pixels */
