//pagination
.pagination {
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style-type: none;

  li {
    margin: 0 2px; }

  a {
    display: block;
    height: 30px;
    line-height: 30px;
    text-align: center;
    text-decoration: none; }

  &__prev,
  &__next {
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: none;
      border: 1px solid $colorPrimary;

      &:hover {
        background: $colorPrimary;

        .ico {
          fill: #fff; } } } }

  &__prev {
    margin: 0 8px 0 0;

    a {
      padding-right: 1px; } }

  &__next {
    margin: 0 0 0 8px;

    a {
      padding-left: 1px; } }

  &__active {
    a {
      color: #2d2d2d;
      cursor: default; } } }

//main icons
.main-icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  li {
    position: relative;
    width: 48%;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 12px;
    line-height: 18px;
    text-transform: uppercase; }

  &__icon {
    height: 36px;

    .ico {
      font-size: 26px; } } }

//small tabs
.simple-tabs {
  position: relative;

  li {
    display: inline-block;
    vertical-align: top;
    cursor: pointer;

    &.magic-line {
      position: absolute;
      left: 0;
      bottom: 0;
      padding: 0;
      margin: 0;
      min-width: 10px;
      max-width: none;
      height: 3px;
      background-color: #0081FF;
      cursor: default; } }

  .current {
    cursor: default; } }

.simple-tabs-box {
  display: none;

  &.visible {
    display: block; } }

.main-icons-section {
  position: relative;
  overflow: hidden;

  .simple-tabs-box {
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    z-index: -1;
    display: block;

    &.visible {
      position: relative;
      top: 0;
      z-index: 0; } } }

// bread-crumbs
.bread-crumbs {
  display: none; }

// user round
.user-round {
  display: flex;
  align-items: center;

  &__avatar {
    position: relative;
    min-width: 30px;
    max-width: 30px;
    height: 30px;
    margin-right: 12px;
    overflow: hidden;
    border-radius: 50%;
    background-color: $colorPrimary;

    img, picture {
      position: absolute;
      z-index: 2;
      left: 50%;
      top: 50%;
      height: 100%;
      min-width: 100%;
      max-width: none;
      transform: translate(-50%, -50%); }

    .ico {
      position: absolute;
      z-index: 1;
      left: 50%;
      bottom: 0;
      font-size: 22px;
      margin-left: -10px;
      fill: $colorPrimaryDark; } }

  &__name {
    font-size: 16px;
    line-height: 21px; } }

.rating-stars {
  display: flex;
  align-items: center;

  &__tt {
    margin-right: 14px;
    font-size: 16px;
    line-height: 28px; }

  &__item {
    display: block;
    height: 20px;
    margin-right: 3px;

    &.hover,
    &.selected {
      .ico {
        fill: $colorWarning; } }

    .ico {
      font-size: 20px;
      fill: $colorGrayLight; } } }

.my-bag-col_val,
.product-price-bl__val,
.pf-buy-price__val {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  line-height: 40px;

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }

  input[type=number] {
    -moz-appearance: textfield; }

  input {
    width: 66px;
    height: 40px;
    padding: 0 5px;
    margin-right: 8px;
    font-size: 18px;
    text-align: center;
    border-radius: 8px;
    border: 1px solid $colorGrayLight; } }

/* Min width 540 pixels */
@media only screen and (min-width: 540px) {
  .pagination {
    max-width: 290px; } }
/* end media min width 540 pixels */

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .main-icons {
    li {
      width: 26%;
      margin-bottom: 30px;
      padding-right: 20px;
      font-size: 14px;
      line-height: 20px; }

    &__icon {
      height: 40px; } }

  .inner-content {
    .bread-crumbs {
      margin-top: -15px; } }

  .bread-crumbs {
    display: block;
    margin-bottom: 26px;

    li {
      position: relative;
      display: inline-block;
      vertical-align: top;
      padding-left: 32px;
      margin-right: 6px;
      color: $colorGray;
      font-size: 16px;
      line-height: 23px;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 23px;
        height: 23px;
        background-color: $colorLight;
        background-image: url('../img/arrow-angle-right.svg');
        background-repeat: no-repeat;
        background-position: 52% 50%;
        background-size: auto 10px;
        border-radius: 50%; }

      &:first-child {
        padding-left: 0;

        &::before {
          display: none; } }

      a {
        display: inline-block;
        vertical-align: top;
        line-height: 23px; } } }

  .my-bag-col_val,
  .product-price-bl__val,
  .pf-buy-price__val {
    input {
      width: 52px; } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .bread-crumbs {
    margin-bottom: 55px; }

  .user-round {
    &__avatar {
      min-width: 60px;
      max-width: 60px;
      height: 60px;
      margin-right: 20px;

      .ico {
        font-size: 48px;
        margin-left: -22px; } }

    &__name {
      font-size: 18px;
      line-height: 24px; } }

  .main-icons {
    li {
      font-size: 14px;
      line-height: 18px; } }

  .my-bag-col_val,
  .product-price-bl__val,
  .pf-buy-price__val {
    input {
      width: 63px; } } }
/* end media min width 980 pixels */

/* Min width 1100 pixels */
@media only screen and (min-width: 1100px) {
  .main-icons {
    flex-wrap: nowrap;

    li {
      min-width: 151px;
      max-width: 151px;
      padding-right: 0;
      margin-right: 0; }

    &__icon {
      height: 44px; } } }
/* end media min width 1100 pixels */

/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .main-icons {
    li {
      min-width: 171px;
      max-width: 171px; } } }
/* end media min width 1200 pixels */
