.my-bag {
  font-size: 18px;
  line-height: 24px;

  &__close {
    position: absolute;
    right: 7px;
    top: 30px;

    &:hover {
      .ico {
        fill: $colorPrimaryDark; } }

    .ico {
      font-size: 16px;
      fill: $colorPrimary; } }

  &__img {
    display: block;
    width: 92px;
    min-height: 60px;
    margin-bottom: 10px;
    border-radius: 4px;
    overflow: hidden;

    img {
      width: 100%; } }

  &__title {
    display: inline-block;
    vertical-align: top;
    font-size: 20px;
    line-height: 28px;
    color: #2D2D2D;

    &:hover {
      color: $colorPrimary; } } }

.my-bag-row {
  padding: 20px 20px 20px 0;
  position: relative;
  border-bottom: 1px solid $colorGrayLight;

  &:last-child {
    border: 0;
    padding-top: 17px;
    padding-bottom: 0; } }

.my-bag-col {
  &_name {
    margin-bottom: 10px; }

  &_val {
    margin-right: 50px; }

  &_price {
    display: inline-block;
    vertical-align: middle; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .my-bag {
    font-size: 20px;
    line-height: 28px;

    &__close {
      top: 31px; }

    &__img {
      float: left;
      width: 80px;
      min-height: 52px;
      margin-bottom: 0; }

    &__title {
      display: block;
      margin-left: 125px; } }

  .my-bag-row {
    display: flex;
    justify-content: space-between;
    min-height: 100px;
    padding-right: 30px;
    padding-bottom: 20px;
    margin-bottom: 10px;

    &:last-child {
      min-height: 20px;
      padding-top: 5px; } }

  .my-bag-col {
    padding-top: 6px;

    &_name {
      overflow: hidden;
      margin-bottom: 0;
      width: 50%;
      padding-right: 20px; }

    &_val {
      width: 25%;
      margin-right: 0;
      padding-top: 0; }

    &_price {
      width: 25%; }

    &_sum {
      width: 25%;
      margin-left: auto;
      padding-top: 0; } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .my-bag {
    padding-top: 10px;
    font-size: 21px;
    line-height: 32px;

    &__close {
      top: 50%;
      margin-top: -8px; }

    &__title {
      font-size: 21px;
      line-height: 32px;
      margin-left: 0; }

    &__img {
      float: none;
      min-width: 80px;
      max-width: 80px;
      margin-right: 20px; } }

  .my-bag-row {
    align-items: center;
    margin-bottom: 0;

    &:last-child {
      min-height: 20px;
      padding-top: 20px;
      margin-bottom: 5px; } }

  .my-bag-col {
    padding-top: 0;

    &_name {
      overflow: visible;
      display: flex;
      align-items: center;
      margin-bottom: 0;
      width: 56%;
      padding-right: 40px; }

    &_val {
      width: 22%; }

    &_price,
    &_sum {
      width: 22%; } } }
/* end media min width 980 pixels */
