.article-content-head {
  position: relative;
  margin-bottom: 20px;

  .tarif-item__labels {
    position: absolute;
    top: auto;
    bottom: -55px;
    left: 10px; }

  .promo-date-wrapp {
    margin: 5px 0 0; }

  h1, .title-site_h1,
  h2, .title-site_h2 {
    margin-bottom: 0; }

  .st-date {
    margin-top: 10px;
    font-size: 14px;
    line-height: 18px; } }

.articles-list {
  li {
    margin-bottom: 30px;
    position: relative; }

  .tarif-item__labels {
    left: 10px;
    top: 10px; }

  &__img {
    display: block;
    margin-bottom: 10px;

    &:hover {
      opacity: .95; }

    img {
      width: 100%; } }

  &__info {
    display: flex;
    flex-direction: column;

    h2, .title-site {
      margin-bottom: 0; } } }

.article-date {
  order: -1;
  margin-bottom: 5px;
  font-size: 14px;
  line-height: 22px; }

.promo-date-wrapp {
  order: -1;
  margin-bottom: 5px;
  font-size: 14px;
  line-height: 21px;

  .article-date {
    margin-bottom: 0; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .article-content-head {
    margin-bottom: 30px;

    .tarif-item__labels {
      float: right;
      margin-top: 10px;
      margin-left: 20px;
      position: relative;
      top: 0;
      bottom: 0;
      left: 0; }

    .promo-date-wrapp {
      margin-top: 10px; }

    .st-date {
      margin-top: 15px; } }

  .articles-list {
    li {
      display: flex; }

    &__img {
      min-width: 263px;
      max-width: 263px;
      margin-bottom: 0;
      margin-right: 10px; }

    &__info {
      flex-grow: 1;
      display: block;

      h2, .title-site_h2 {
        margin-bottom: 12px; } } }

  .article-date {
    margin-bottom: 0; }

  .promo-date-wrapp {
    font-size: 14px;
    line-height: 22px;

    .article-date {
      display: inline;
      margin-right: 15px; } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .articles-wrapper {
    margin-bottom: 20px; }

  .articles-list {
    margin-bottom: 50px;

    &__img {
      min-width: 478px;
      max-width: 478px;
      margin-right: 20px; }

    &__info {
      padding-top: 18px;

      h2, .title-site_h2 {
        margin-bottom: 24px; } } } }
/* end media min width 980 pixels */
