.delivery-section {
  padding: 20px 0 30px;

  h2, .title-site {
    margin-bottom: 20px; } }

.delivery-form {
  .btn {
    margin-top: 10px; }

  .form-st-checkbox {
    margin-bottom: 20px; }

  .checked {
    .delivery-fields {
      display: block; } } }

.delivery-fields {
  display: none;
  margin-bottom: 20px;

  .field-item__field {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0; }

    input {
      font-size: 18px; } }

  textarea {
    height: 100px;
    border-radius: 4px;
    font-size: 18px; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .delivery-section {
    padding: 30px 0 60px; }

  .delivery-form {
    position: relative;
    min-height: 240px;
    padding-top: 10px;
    padding-right: 370px;

    .btn {
      margin-top: 20px; } }

  .delivery-fields {
    margin-bottom: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: 354px; } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .delivery-section {
    h2, .title-site {
      margin-bottom: 36px; } }

  .delivery-form {
    padding-right: 610px;

    .btn {
      margin-top: 30px; } }

  .delivery-fields {
    width: 580px; } }
/* end media min width 980 pixels */
