.search-line {
  position: relative;

  input {
    font-size: 16px;
    height: 50px;
    padding-right: 60px;
    border-radius: 0;
    background-color: $colorLight; }

  button {
    position: absolute;
    right: 13px;
    top: 50%;
    margin-top: -20px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;

    .ico {
      font-size: 26px;
      fill: $colorPrimary;
      transition: $transDefault;

      &:hover {
        fill: $colorPrimaryDark; } } } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .search-line {
    input {
      font-size: 18px;
      height: 60px;
      border-radius: 4px;
      background-color: #fff; } } }
/* end media min width 740 pixels */
