.contacts-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 10px;

  li {
    min-width: 50%;
    padding-right: 10px;
    margin-bottom: 10px;
    font-size: 16px;
    line-height: 21px; }

  span {
    display: block;
    white-space: nowrap; }

  b {
    font-weight: 600; } }

.contats-wrapper {
  padding: 30px 0 20px;

  .ol-list-num {
    margin-bottom: 30px;

    li {
      margin-bottom: 15px;
      padding-left: 43px;
      font-size: 16px;
      line-height: 21px;

      &::before {
        top: 2px;
        width: 33px;
        height: 33px;
        line-height: 33px;
        font-size: 18px;
        border: none; } } } }

.contacts-nav-options {
  margin-top: 20px;
  margin-bottom: 20px;

  li {
    position: relative;
    padding-left: 30px;
    margin-bottom: 13px;
    font-size: 16px;
    line-height: 21px; }

  .ico {
    position: absolute;
    left: 0;
    top: 0;
    fill: $colorPrimary; }

  .ico-mono-nav-on-foot {
    margin-top: -2px;
    font-size: 24px; }

  .ico-mono-nav-bus {
    font-size: 21px; }

  .ico-mono-nav-tram {
    font-size: 27px; } }

.contacts-map {
  overflow: hidden;
  margin-left: -12px;
  margin-right: -12px;
  margin-bottom: 13px;
  border-radius: 3px;

  img {
    width: 100%; } }

.swiper-contacts {
  margin-left: -12px;
  margin-right: -12px;

  .swiper-slide {
    transition: all 0ms ease 0s;
    opacity: 0 !important;

    &.swiper-slide-active {
      opacity: 1 !important; } }

  &__tt {
    padding: 0 12px 10px;
    font-size: 18px;
    line-height: 24px; }

  &__text {
    padding: 10px 12px 0;
    margin: 0; }

  img {
    width: 100%; }

  p {
    margin: 0;
    font-size: 18px;
    line-height: 24px; }

  .swiper-nav-wrapper {
    margin: 10px 12px 0;
    justify-content: flex-start; }

  .swiper-button-prev,
  .swiper-button-next {
    display: flex-inline;
    margin: 0; }

  .swiper-button-next {
    margin-left: 10px; } }

/* Min width 740 pixels */
@media only screen and (min-width: 740px) {
  .contacts-info {
    padding-bottom: 0;

    li {
      min-width: 182px;
      margin-bottom: 20px;

      &:first-child {
        flex-grow: 1; } } }

  .contats-wrapper {
    padding-top: 35px;

    .ol-list-num {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      li {
        width: 49%;
        padding-left: 0;
        margin-bottom: 13px;

        &::before {
          display: block;
          width: 42px;
          height: 42px;
          margin-bottom: 15px;
          font-size: 20px;
          line-height: 42px;
          position: relative;
          top: 0; } } } }

  .contacts-nav-options {
    margin-top: 24px;
    margin-bottom: 32px;

    li {
      margin-bottom: 15px; }

    .ico-mono-nav-tram {
      margin-top: -3px; } }

  .contacts-map {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 15px; }

  .swiper-contacts {
    margin-left: 0;
    margin-right: 0;

    &__tt {
      padding: 0;
      margin-bottom: 15px;
      font-size: 20px;
      line-height: 28px; }

    &__text {
      padding: 65px 0 0;
      margin: 0 0 0 365px; }

    img {
      float: left;
      width: 354px;
      border-radius: 20px; }

    .swiper-nav-wrapper {
      margin: 0;
      position: absolute;
      top: 48px;
      left: 365px;
      z-index: 3; } } }
/* end media min width 740 pixels */

/* Min width 980 pixels */
@media only screen and (min-width: 980px) {
  .contacts-info {
    li {
      min-width: 200px;
      margin-bottom: 24px;
      font-size: 18px;
      line-height: 24px; } }

  .contats-wrapper {
    padding-top: 32px;
    padding-bottom: 30px;

    .ol-list-num {
      li {
        margin-bottom: 10px;
        font-size: 18px;
        line-height: 24px; } } }

  .contacts-nav-options {
    margin-top: 20px;
    display: flex;
    align-items: center;
    margin-left: -25px;

    li {
      margin-left: 25px;
      min-width: 172px;
      margin-bottom: 0;
      padding-left: 50px;
      font-size: 18px;
      line-height: 24px; }

    .ico-mono-nav-on-foot {
      margin-top: 0;
      top: 50%;
      margin-top: -22px;
      font-size: 44px; }

    .ico-mono-nav-bus {
      font-size: 41px;
      margin-top: 4px; }

    .ico-mono-nav-tram {
      font-size: 47px;
      margin-top: 0px; } }

  .contacts-map {
    margin-bottom: 30px; }

  .swiper-contacts {
    &__tt {
      margin-bottom: 24px;
      font-size: 21px;
      line-height: 32px; }

    &__text {
      padding-top: 70px;
      margin: 0 0 0 598px; }

    img {
      width: 578px; }

    .swiper-nav-wrapper {
      top: 66px;
      left: 598px; } } }
/* end media min width 980 pixels */

/* Min width 1100 pixels */
@media only screen and (min-width: 1100px) {
  .contacts-info {
    justify-content: flex-start;

    li {
      &:first-child {
        flex-grow: 0;
        min-width: 400px;
        max-width: 400px; } } } }
/* end media min width 1100 pixels */

/* Min width 1250 pixels */
@media only screen and (min-width: 1250px) {
  .contacts-nav-options {
    li {
      min-width: 477px;

      &:first-child {
        min-width: 172px;
        padding-right: 0; } } } }
/* end media min width 1250 pixels */
